.App {
  text-align: center;
  list-style: none;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: rgb(45, 45, 61);
  color: white;
  list-style: none;
}

.nav-links {
  width: 10%;
